<template>
  <div class="index">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="btn-block">
        <div @click="showBatchDialog()" class="btn-main mr15">批量设置</div>
        <div @click="showDialog()" class="btn-main-deepen">新增班级</div>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>

      <resize-table>
        <table class="table">
        <thead>
        <tr>
          <th width="5%">
            <el-checkbox v-model="checkAll"></el-checkbox>
          </th>
          <th width="8%">年级</th>
          <th width="10%">班级</th>
          <th width="10%">类型</th>
          <th width="10%">班主任</th>
          <th width="12%">班主任手机号</th>
          <th width="15%">班主任助理</th>
          <th width="12%">班助手机号</th>
          <th width="10%">状态</th>
          <th width="8%">操作</th>
        </tr></thead>
        <tbody>
        <tr v-for="(item,key) in table.list">
          <td>
            <el-checkbox v-model="checkboxList[key]"></el-checkbox>
          </td>
          <td>{{item.grade}}</td>
          <td>{{item.aliasName}}</td>
          <td>{{$enums.ClassType.getName(item.classType)}}</td>
          <td>
            <p v-if="!!item.advisers && item.advisers.length > 0">{{item.advisers[0].name}}</p>
            <p v-else>--</p>
          </td>
          <td>
            <p v-if="item.advisers && item.advisers.length > 0">{{item.advisers[0].phone}}</p>
            <p v-else>--</p>
          </td>
          <td>
            <div v-if="!!item.assistants && item.assistants.length > 0">
              <p v-for="i in item.assistants">{{i.name}}</p>
            </div>
            <p v-else>--</p>
          </td>
          <td>
            <div v-if="!!item.assistants && item.assistants.length > 0">
              <p v-for="i in item.assistants">{{i.phone}}</p>
            </div>
            <p v-else>--</p>
          </td>
          <td>{{$enums.ClassStatus.getName(item.classStatus)}}</td>
          <td>
            <span @click="showDialog(item)" class="oper">编辑</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
      <!--新增 编辑班级弹窗-->
      <el-dialog :title="title" :visible.sync="dialog" width="540px" class="dialog">
        <div class="item">
          <span class="title imp">班级状态</span>
          <el-radio-group v-model="form.classStatus">
            <el-radio :key="key" v-for="(item,key) in $enums.ClassStatus.list" :label="item.value" border>&nbsp;&nbsp;{{item.label}}&nbsp;&nbsp;</el-radio>
          </el-radio-group>
        </div>
        <div class="item">
          <span class="title imp">班级类型</span>
          <el-radio-group v-model="form.classType">
            <el-radio :key="key" v-for="(item,key) in $enums.ClassType.list" :label="item.value" border>{{item.label}}</el-radio>
          </el-radio-group>
        </div>
        <div class="item">
          <span class="title imp">班级名称</span>
          <el-input class="input-class" type="number" v-model="form.className" placeholder="请输入班级名称"></el-input>
          <span v-show="form.className" class="text">{{form.gradeId < 4 ? '初': '高'}}{{form.className}}</span>
        </div>
        <div class="item">
          <span class="title imp">所属年级</span>
          <el-select class="input" default-first-option filterable v-model="form.gradeId">
            <el-option
              v-for="item in searchData[0].option"
              :value="item.value" :label="item.label" :key="item.value"
            ></el-option>
          </el-select>
        </div>
        <div class="item">
          <span class="title imp">班主任</span>
          <el-select
              v-model="form.adviserIds"
              class="input"
              filterable
              default-first-option
              remote
              clearable
              reserve-keyword
              placeholder="请输入教职工关键词"
              :remote-method="getStaff"
              :loading="loading">
            <el-option
                v-for="item in staffList"
                :key="item.id"
                :label="item.name"
                :disabled="form.assistantIds.indexOf(item.id) !== -1"
                :value="item.id">
              <span style="float: left">{{ item.name }}</span>
              <span style="float: right; color: #8492a6; font-size: 13px">{{item.idCard}}/{{item.phone}}</span>
            </el-option>
          </el-select>
        </div>
        <div class="item">
          <span class="title">班主任助理</span>
          <el-select
              v-model="form.assistantIds"
              class="input"
              multiple
              filterable
              default-first-option
              remote
              clearable
              reserve-keyword
              placeholder="请输入教职工关键词"
              :remote-method="getStaff"
              :loading="loading">
            <el-option
                v-for="item in staffList"
                :key="item.id"
                :disabled="item.id === form.adviserIds"
                :label="item.name"
                :value="item.id">
              <span style="float: left">{{ item.name }}</span>
              <span style="float: right; color: #8492a6; font-size: 13px">{{item.idCard}}/{{item.phone}}</span>
            </el-option>
          </el-select>
        </div>
        <div class="btns">
          <div @click="addClass()" class="btn-submit btn-add-save mr15">提交</div>
          <div @click="dialog = false" class="btn-clear">取消</div>
        </div>
      </el-dialog>
      <!--批量设置年级弹窗-->
      <el-dialog title="批量设置" :visible.sync="batchDialog" width="540px" class="dialog">
        <div class="item">
          <span class="title imp">班级状态</span>
          <el-radio-group v-model="bathForm.classStatus">
            <el-radio :key="key" v-for="(item,key) in $enums.ClassStatus.list" :label="item.value" border>&nbsp;&nbsp;{{item.label}}&nbsp;&nbsp;</el-radio>
          </el-radio-group>
        </div>
        <div class="item">
          <span class="title imp">所属年级</span>
          <el-select default-first-option class="input" clearable filterable v-model="bathForm.gradeId">
            <el-option
                v-for="item in searchData[0].option"
                :value="item.value" :label="item.label" :key="item.value"
            ></el-option>
          </el-select>
        </div>
        <div class="btns">
          <div @click.once="setClass()" class="btn-submit mr15">提交</div>
          <div @click="batchDialog = false" class="btn-clear">取消</div>
        </div>
      </el-dialog>
    </main-part>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "班级管理", path: ""}],
        searchData: [
          {type: 'select', title: '所属年级', model: 'gradeId', option: []},
          {type: 'select', title: '班级状态', model: 'classStatus', option: this.$enums.ClassStatus.list},
          {type: 'input', placeholder: '班级名称'}
        ],
        checkboxList: [false],
        loading: false,
        title: '新增班级',
        dialog: false,
        table: {
          url: '/class/queryClassPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            classStatus: null,
            gradeId: null,
            keywords: null
          }
        },
        // 所有的教职工
        staffList: [],
        form:{
          className: '',
          classStatus: 'UN_GRADUATED',
          classType: 'PARALLEL_CLASS',
          gradeId: '',
          adviserIds: [],
          assistantIds: []
        },
        // 批量设置弹窗
        batchDialog:false,
        // 批量表单
        bathForm: {
          classStatus: 'UN_GRADUATED',
          gradeId: 1,
          classIds: []
        }
      }
    },
    methods: {
      // 展现班级弹窗
      async showDialog(item){
        await this.getStaff()
        if(item){
          this.title = '编辑班级'
          this.form = {
            classId: item.classId,
            className: item.className,
            classStatus: item.classStatus,
            gradeId: item.gradeId,
            classType: item.classType,
            adviserIds:  null,
            assistantIds: []
          }
          if(item.advisers){
            item.advisers.forEach(e=>{
              this.staffList.push(e)
            })
          }
          if(item.assistants){
            item.assistants.forEach(e=>{
              this.staffList.push(e)
            })
          }

          if(item.advisers){
              this.form.adviserIds = item.advisers[0].id
          }
          if(item.assistants && item.assistants.length){
            item.assistants.forEach(e=>{
              this.form.assistantIds.push(e.id)
            })
          }
        }else{
          this.title = '新增班级'
          this.form = {
            className: '',
            classStatus: 'UN_GRADUATED',
            classType: 'PARALLEL_CLASS',
            gradeId: 1,
            adviserIds: null,
            assistantIds: []
          }
        }
        this.dialog = true
      },
      // 展现批量设置弹窗
      showBatchDialog(){
        if(!this.checkboxList.some(item => item)){
          this.$message.warning('请选择班级')
          return
        }
        this.batchDialog = true
        // 判断多选框选择了哪些班级
        this.bathForm.classIds = []
        this.checkboxList.forEach((e,i)=>{
          if(e){
            this.bathForm.classIds.push(this.table.list[i].classId)
          }
        })
        this.bathForm.classStatus = 'UN_GRADUATED'
        this.bathForm.gradeId = null
      },
      // 批量设置班级
      async setClass(){
        if(!this.bathForm.classStatus){
          this.$message.warning('请设置班级状态')
          return
        }
        if(!this.bathForm.gradeId){
          this.$message.warning('请选择年级')
          return
        }
        await this.$ajax({
          url: '/class/batchUpdateClass.do',
          query: this.bathForm
        })
        this.$remind('设置成功')
        this.$search(this.table)
        this.batchDialog = false
      },
      // 新增 修改班级
      async addClass(){
        // if(!this.form.className){
        //   this.$message.warning('请输入班级名称')
        //   return
        // }
        if(isNaN(this.form.className)){
          this.$message.warning('班级名称必须为数字')
          return
        }
        if(!this.form.classStatus){
          this.$message.warning('请选择班级状态')
          return
        }
        if(!this.form.gradeId){
          this.$message.warning('请选择所属年级')
          return
        }
        if(!this.form.adviserIds){
          this.$message.warning('请选择班主任')
          return
        }
        let query = JSON.parse(JSON.stringify(this.form))
        query.adviserIds = [query.adviserIds]
        // 如果是编辑班级
        let url = '/class/addClass.do'
        let text = '新增成功'
        if(this.form.classId){
          url = '/class/updateClass.do',
          text = '编辑成功'
        }
        await this.$ajax({
          url,
          el: '.btn-add-save',
          noText: true,
          query
        })
        this.$search(this.table)
        this.$remind(text)
        this.dialog = false
      },
      // 获取年级列表
      async getGradeList() {
        let res = await this.$ajax({
          url: '/grade/queryGradeList.do',
        })
        this.searchData[0].option = []
        res.forEach(e => {
          this.searchData[0].option.push({
            label: e.name,
            value: e.id
          })
        })
      },
      // 获取教职工成员
      async getStaff(query){
        if(query !== ''){
          this.loading = true
          let res = await this.$ajax({
            url: '/user/queryStaffPage.do',
            query:{
              keywords: query,
              pageNo: 1,
              pageSize: 15
            }
          })
          this.loading = false
          this.staffList = res.list
        }else{
          this.staffList = []
        }
      }
    },
    watch: {
      'table.list': function () {
        this.checkboxList = new Array(this.table.list.length).fill(false)
      }
    },
    computed: {
      checkAll: {
        get: function () {
          if (this.checkboxList.length > 0) {
            return this.checkboxList.every(item => item)
          } else {
            return false
          }
        },
        set: function (val) {
          this.checkboxList = new Array(this.table.list.length).fill(val)
        }
      }
    },
    mounted() {
      // 取消选择所有的多选框
      this.checkboxList = new Array(this.table.list.length).fill(false)
      this.$search(this.table)
      this.getGradeList()
      // this.getStaff()
    }
  }
</script>
<style lang="sass" scoped>
  .index

    .dialog
      .title
        width: 80px
        line-height: 40px
        vertical-align: top
        display: inline-block
      .imp:before
        content: '*'
        color: $error-c
        margin-right: 4px
      .input
        width: calc(100% - 80px)
      .input-class
        margin-right: 10px
        width: calc(100% - 150px)
      .text
        color: $main-c
      .item
        margin-bottom: 20px
      .btns
        text-align: center
</style>
